.blog-comment-text:before {
	content: '';
	height: 0;
	width: 0;
	position: absolute;
	display: block;
	border-style: solid;
	top: 25px;
	border-width: 11px 13px 11px 0;
	border-color: transparent #EEF3FA;
	left: -12px;
}

.blog-comment-heading {
	display: flex;
	align-items: center
}

.blog-comment-date p {
	margin-bottom: 15px;
	color: #111
}

.blog-comment-date p i {
	margin-right: 10px;
	color: #B78D65
}

.blog-leave-comment {
	margin-top: 50px
}

.comment-form-group {
	position: relative;
	width: 100%
}

.comment-form-group input {
	background: #EEF3FA;
	padding: 0px 10px 0px 40px;
}

.comment-form-text textarea {
	background: #EEF3FA;
	height: 200px;
	margin-top: 20px;
	padding: 20px
}

.comment-form-group i {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	left: 10px
}

.blog-comment-submit {
	text-align: center;
	margin-top: 50px
}


